<template>
    <view class="intelligent_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">选货发布</block></cu-custom>
        <view class="things_width_full freight_list">
            <view class="flex justify-between align-start things_width_full freight_item" v-for="(item,index) in freightData.list" v-bind:key="index">
                <view class="things_width_full box_top">
                    <view class="start_address">装载地点：{{item.loadingAddress}}</view>
                    <view class="end_address">卸货地点：{{item.unloadingAddress}}</view>
                    <view class="flex justify-between align-center double_box">
                        <view class="freight_type">货物类型：{{item.goodsType}}</view>
                        <view class="freight_weight">重量：{{item.weight}}吨</view>
                    </view>
                    <view class="single_text_item">运输价格：￥{{item.weight}}</view>
                    <view class="single_text_item">货物介质：{{item.goods_medium}}</view>
                    <view class="single_text_item">损耗范围：{{item.loss_range}}</view>
                    <view class="single_text_item">开始时间：{{item.startTime}}</view>
                    <view class="single_text_item">结束时间：{{item.endTime}}</view>
                    <view class="flex justify-end">
                        <button class="cu-btn round things_bg_active_color select_btn" :data-id="item.id" @tap="linkToFreightForm"> 选择 </button>
                    </view>
                </view>
                <!-- 按钮组 -->
                <!-- <view class="things_width_full flex justify-between align-center btn_box">
                    <button class="cu-btn bg-white"><image class="del_icon" src="../../../../../static/images/global/del.png" mode="widthFix"></image><text class="btn_text">删除</text></button>
                    <button class="cu-btn bg-white"><image class="update_icon" src="../../../../../static/images/global/update.png" mode="widthFix"></image><text class="btn_text">修改</text></button>
                </view> -->
            </view>
            <view v-if="freightData.list.length <= 0" class="things_width_full kong_data">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_tip">暂无数据</view>
            </view>
            <!-- 新增按钮 -->
            <!-- <view class="things_width_full flex justify-center align-center add_btn_box">
                <button class="things_width_full cu-btn">新增货物</button>
            </view> -->
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { getGoodsCollectionList_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { identity_format } from '@/utils/filters.js';
import checkForm from '@/utils/checkFormData.js';
let that = null;
export default {
    data() {
        return {
            freightData: {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            },
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.getFreightList();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.freightData = {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            };
        },
        /**
         * @description: 关闭当前页面，跳转到发布货车表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToFreightForm(e) {
            uni.redirectTo({
                url: `/pages/things/publish/freight/index?goods_id=${e.currentTarget.dataset.id}`
            });
        },
        /**获取收藏货车列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getFreightList(e) {
            uni.showLoading({
                title: '加载中'
            });
            let params = {
                page: that.freightData.page,
                page_size: that.freightData.page_size
            };
            getGoodsCollectionList_api(params,that).then(res=>{
                that.freightData.list = that.freightData.list.concat(res.list);
                if (res.total >= that.freightData.list.length) {
                    that.freightData.pageMore = false;
                }else{
                    that.freightData.page++;
                }
                uni.hideLoading();
            }).catch(err=>{
                console.log(err);
                uni.hideLoading();
            })
        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        // 请求
        that.getFreightList();
    },
    /**上拉加载
     * @description: 
     * @param {*}
     * @return {*}
     * @author: gitxinghui
     */
    onReachBottom: function(e) {
        if (that.freightData.pageMore) that.getCarList();
    }
}
</script>
<style lang="scss" scoped>
    @import './index';
</style>